import {LOAD_MY_NOTIFICATIONS, RECEIVE_NEW_NOTIFICATIONS} from "../../actions/notifications";

const initialState = {
  notifications: [],
  newNotifications: []
}


const notificationReducer = (state = {...initialState}, action) => {
  switch (action.type) {
    case LOAD_MY_NOTIFICATIONS :
      return {...state, notifications: action.payload}
    case RECEIVE_NEW_NOTIFICATIONS :
      return {...state, newNotifications: action.payload}
    default:
      return state;
  }
}

export default notificationReducer
