export const checkReceiveNewNotification = () => {
  return async (dispatch) => {
    const res = await NotificationService.checkNewNotification();
    if (!res || res.status !== 200) {
      return
    }
    dispatch({type: RECEIVE_NEW_NOTIFICATIONS, payload: res.data})
  }
}

