import * as Icon from "react-feather";
import React, {useEffect, useState} from "react";
import PerfectScrollbar from "react-perfect-scrollbar";
import {useDispatch, useSelector} from "react-redux";
import {getMyNotification} from "../../../redux/actions/notifications";
import {Button, DropdownItem, Media, Modal, ModalBody, ModalFooter, ModalHeader, Toast} from "reactstrap";
import moment from "moment";
import {FormattedMessage, useIntl} from "react-intl";
import 'moment/locale/vi';
import {NotificationService} from "../../../services/notificationService";


const Notifications = () => {

  const dispatch = useDispatch()
  const intl = useIntl()
  const {notifications} = useSelector(state => state.notifications)
  const [notificationModal, setNotificationModal] = useState(false)
  const [notification, setNotification] = useState(null)

  useEffect(() => {
    dispatch(getMyNotification())
  }, [])

  const onClickOpenNotification = (item) => {
    setNotification(item)
    setNotificationModal(true)
    // change notification status
    const notificationRequest = notifications.find(notification => notification.id === item.id)
    const notificationAllStatus = NotificationService.updateAllNotificationStatus({
      notificationId: notificationRequest.id,
      status: 'READ'
    })
    if(notificationAllStatus.status === 200) return
  }

  const onClickUpdateAllNotificationStatus = () => {
    // set all notification status to read
    const notificationAllStatus = NotificationService.updateAllNotificationStatus()
    if(notificationAllStatus.status === 200) return
  }

  return (
    <React.Fragment>
      <li className='dropdown-menu-header'>
        <div className='dropdown-header mt-0 text-left'>
          <span className='notification-title'><FormattedMessage id='menu.notification'/></span>
          <span>({notifications.length})</span>
        </div>
      </li>
      <PerfectScrollbar
        className='media-list overflow-hidden position-relative'
        options={{
          wheelPropagation: false
        }}
      >
        {
          notifications.map(item => (
            <div className='d-flex justify-content-between' key={item.id} onClick={() => onClickOpenNotification(item)}>
              <Media className='d-flex align-items-start'>
                <Media left href='#'>
                  <Icon.Server
                    className='font-medium-5 primary'
                    size={21}
                  />
                </Media>
                <Media body>
                  <Media heading className='primary media-heading' tag='h6'>
                    <div className={!item.read ? 'font-weight-bold' : ''}>
                      {item.title}
                    </div>
                  </Media>
                  <div className={!item.read ? 'font-weight-bold' : ''}
                       dangerouslySetInnerHTML={{__html: item.shortContent}}/>
                </Media>
                <small className="mt-1">
                  <time
                    className='media-meta'
                    dateTime={item.sendDate}
                  >
                    {
                      moment().diff(moment(item.sendDate), 'days') >= 1 ?
                        moment(item.sendDate).format("DD/MM/YYYY") : moment(item.sendDate).fromNow()
                    }
                  </time>
                </small>
              </Media>
            </div>
          ))
        }

      </PerfectScrollbar>
      {
        notifications.length > 0 && <li className='dropdown-menu-footer' onClick={onClickUpdateAllNotificationStatus}>
          <DropdownItem tag='a' className='p-1 text-center'>
            <span className='align-middle'>
              <FormattedMessage id="menu.readAll" />
            </span>
          </DropdownItem>
        </li>
      }

      {notification && <Modal isOpen={notificationModal} toggle={() => setNotificationModal(!notificationModal)} className='modal-dialog-centered'>
        <ModalHeader toggle={() => setNotificationModal(!notificationModal)}>
          <FormattedMessage id="menu.notification" />
        </ModalHeader>
        <ModalBody>
          <div dangerouslySetInnerHTML={{__html: notification.title}}/>
          <div dangerouslySetInnerHTML={{__html: notification.content}}/>
        </ModalBody>
      </Modal>}

    </React.Fragment>
  )
}

export default Notifications
