import {
  API_CHECK_NEW_NOTIFICATIONS,
  API_GET_MY_NOTIFICATIONS,
  API_GET_NAV_CONFIGS, API_GET_NOTIFICATION_FROM_ESPUBLIC,
  API_GET_USER_ROLES, API_UPDATE_ALL_NOTIFICATION_STATUS, API_UPDATE_NOTIFICATION
} from '../configs/app-configs'
import { generateUUID } from '../utility'
import HttpClient from '../utility/HttpClient'

export class NotificationService {
  static getMyNotifications = () => {
    return HttpClient.get(API_GET_NOTIFICATION_FROM_ESPUBLIC, {
      params: { uuid: generateUUID() },
      isBackgroundRequest: true
    })
  }

  static checkNewNotification = () => {
    return HttpClient.get(API_CHECK_NEW_NOTIFICATIONS, {
      params: { uuid: generateUUID() },
      isBackgroundRequest: true
    })
  }

  static updateNotificationStatus = (notification) => {
    return HttpClient.put(API_UPDATE_NOTIFICATION, notification, {
      isBackgroundRequest: true
    })
  }

  static updateAllNotificationStatus = () => {
    return HttpClient.put(API_UPDATE_ALL_NOTIFICATION_STATUS, {}, {
      params: 'READ',
      isBackgroundRequest: true
    })
  }
}
